function getUrlJoinParams(url, obj) {
  var result = url + '?';
  for (var p in obj) {
    if (typeof obj[p] === 'function') {
      continue;
    } else {
      result += encodeURI(p) + '=' + encodeURI(obj[p]) + '&';
    }
  }
  result = result.substring(0, result.length - 1);
  return result;
};

