<template>
  <div class="content">
    <div @click="gotoHome" class="left hand">系统名称</div>
    <div class="right">
      <div @click="gotoFee" class="addFee blue hand">新增交易</div>
      <div v-if="hasLogin && (userInfo.role == 'admin' || userInfo.role == 'finance')" style="display: flex">
        <div @click="gotoFinance" class="blue hand" style="margin-right: 20px">财务主页</div>
        <div @click="gotoRecord" class="blue hand">交易流水</div>
      </div>
      <div v-if="!hasLogin">未登录</div>
      <div v-if="hasLogin"  class="username">{{userInfo.name}}</div>
      <div v-if="hasLogin" @click="quit" class="hand">退出</div>
    </div>
  </div>
</template>
<style scoped>
  .content{
    width: 1100px;
    padding: 0 50px;
    height: 100px;
    margin: 0 auto;
    line-height: 100px;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
  }

  .right{
    width: 300px;
    display: flex;
    justify-content: space-between;
  }
  .blue{
    color: #00C6FF;
  }
</style>
<script>
  import { removeStore } from "../utils/util"
  import { mapState, mapMutations } from "vuex"
  export default {
    data:function () {
      return{

      }
    },
    computed: {
      ...mapState(["hasLogin", "userInfo"])
    },
    mounted() {

    },
    methods:{
      ...mapMutations(["INIT_USER", "EMPTY_USER", "CHANGE_LOGIN"]),
      gotoHome() {
        if (!this.hasLogin) {
          this.$router.push("/");
        }else {
          this.$router.push("/home");
        }
      },
      gotoFinance() {
        this.$router.push("/finance");
      },
      gotoFee() {
        this.$router.push("/personal");
      },
      gotoRecord() {
        this.$router.push("/record");
      },
      quit() {
        this.EMPTY_USER();
        this.CHANGE_LOGIN(false);
        removeStore("userInfo");
        this.$router.replace('/');
      }
    }
  }
</script>




