import Vue from 'vue'
import VueRouter from 'vue-router'
import { routerMode } from '../utils/env'

const index = r => require.ensure([], () => r(require('../pages/index/index')), 'index')
const home = r => require.ensure([], () => r(require('../pages/home/index2')), 'home')
const login = r => require.ensure([], () => r(require('../pages/login/index')), 'login')
const personal = r => require.ensure([], () => r(require('../pages/personal/index')), 'personal')
const finance = r => require.ensure([], () => r(require('../pages/finance/index')), 'finance')
const record = r => require.ensure([], () => r(require('../pages/record/index')), 'record')

Vue.use(VueRouter)

export default new VueRouter({
  mode: routerMode,
  routes: [
    {
      path: '/',
      component: index,
      children: [
        { path: '', redirect: 'login'},
        { path: 'login', component: login},
        { path: 'home', component: home},
        { path: 'personal', component: personal},
        { path: 'finance', component: finance},
        { path: 'record', component: record}
      ]
    }
  ]
})



